import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    rowData: []
  },
  actions: {
    FETCH_MEMO (ctx, method, url) {
      console.log('FETCH_MEMO');
    
      return axios({
        method,
        url
      })
    },
    CREATE_MEMO (ctx, method, url, data) {
      return axios({
        method,
        url,
        data
      })
    },
    DELETE_MEMO (ctx, method, url) {
      return axios({
        method,
        url,
      })
    }
  },
  mutations: {
  },
  modules: {
  }
})
